import { Route, RouteProps, Redirect } from "react-router-dom";
import { getAccessToken, setAccessToken } from "Auth/accessToken";
import jwtDecode from "jwt-decode";
import { checkAuth } from "Auth/CheckAuth";
import { useEffect, useState } from "react";
import URI from "URI";

interface IAuthRouteProps extends RouteProps {
    component: any;
}

const AuthRoute: React.FC<IAuthRouteProps> = ({
    component: Component,
    ...rest
}) => {
    const [pageLoader, setPageLoader] = useState(true);
    const currentRefreshToken = localStorage.getItem("iqj1") as string;

    useEffect(() => {
        try {
            //get new accessToken & refreshToken with fetch to URI/refresh_token
            fetch(`${URI}/refresh_token`, {
                method: "POST",
                credentials: "include",
                headers: {
                    refreshToken: currentRefreshToken,
                },
            }).then(async res => {
                const { accessToken, refreshToken } = await res.json();
                setAccessToken(accessToken);
                localStorage.setItem("iqj1", refreshToken);
                setPageLoader(false);
            });
        } catch {
            setPageLoader(false);
        }
    }, []);

    if (pageLoader) {
        return <></>;
    } else {
    }

    return (
        <Route
            {...rest}
            render={props =>
                checkAuth() ? <Component {...props} /> : <Redirect to='/' />
            }
        />
    );
};

export default AuthRoute;
