import anime from "animejs";
import { useEffect, useRef } from "react";
import DazzleCrewLogo from "images/DazzleCrewLogo.png";
import Star from "images/star.png";
import "css/logo.scss";

const Logo = () => {
    const starAnimationRef = useRef() as any;

    function createNewAnimation() {
        starAnimationRef.current = anime
            .timeline({
                targets: ".star-1",
                loop: true,
                easing: "easeInOutQuad",
                loopComplete: anim => {
                    anim.pause();

                    setTimeout(() => {
                        anim.restart();
                        if (anim.paused) {
                            return createNewAnimation();
                        }
                    }, Math.floor(Math.random() * 25000 + 10000));
                },
            })
            .add({
                opacity: 1,
            })
            .add({
                scale: [0, 1],
                duration: 1000,
            })
            .add({ delay: 500, opacity: 0, duration: 1500 })
            .add({ delay: 1000, scale: [1, 0] });
    }

    useEffect(() => {
        createNewAnimation();
    });

    return (
        <div className='animated-logo-container'>
            <span>
                <img className='star-1' src={Star} alt='Star' />
                <img
                    className='dc-logo'
                    src={DazzleCrewLogo}
                    alt='Dazzle Crew Logo'
                />
            </span>
        </div>
    );
};

export default Logo;
