import { Route, Redirect, Switch } from "react-router-dom";
import { lazy, Suspense } from "react";

import AuthRoute from "./AuthRoute";

const Cart = lazy(() => import("components/cart/Cart"));
const UsersOrders = lazy(() => import("components/orders/UsersOrders"));
// const ProductDetails = lazy(() => import("components/Products/ProductDetails"));
// const Products = lazy(() => import("components/Products/Products"));
const Sections = lazy(() => import("components/mobileNavbar/Sections"));
const Products1 = lazy(() => import("components/Products/Products1"));
const FAQ = lazy(() => import("components/FAQ"));
const CheckoutButton = lazy(() => import("components/checkout/CheckoutButton"));

export const Routes = () => {
    return (
        <Switch>
            <Suspense fallback={<>...loading</>}>
                <AuthRoute
                    path='/my-orders'
                    component={() => <UsersOrders />}
                />

                <Route path='/section' render={() => <Sections />} />
                <Route path='/products' render={() => <Products1 />} />

                {/* <Route path='/product:id' render={() => <ProductDetails />} /> */}
                <Route path='/cart' render={() => <Cart />} />

                <Route path='/faq' render={() => <FAQ />} />

                {/* TEST ROUTES FOR DEVELOPMENT */}
                <Route path='/cbtn' render={() => <CheckoutButton />} />
                {/* <Route path='/products1' render={() => <Products1 />} /> */}
                {/* <Route path='/error-1' render={() => <ErrorPage />} />
            <Route path='/error-2' render={() => <ErrorPage2 />} />
            <Route path='/error-3' render={() => <ErrorPage3 />} /> */}

                <Route render={() => <Redirect to='/products' />} />
            </Suspense>
        </Switch>
    );
};
