import GoogleAuthBtn from "./GoogleAuthBtn";
import { checkAuth } from "Auth/CheckAuth";
import Logo from "./Logo";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faShoppingCart,
    faBars,
    faSignOutAlt,
} from "@fortawesome/free-solid-svg-icons";
import anime from "animejs";
import { useRef } from "react";
import { setAccessToken } from "Auth/accessToken";

const MobileNavbar = () => {
    const timelineRef = useRef() as any;

    return (
        <>
            <nav>
                <div className='vertical-align'>
                    <label htmlFor='toggleMenu'>
                        <FontAwesomeIcon id='mobile-nav-icon' icon={faBars} />
                    </label>
                </div>

                <Logo />

                <span>
                    <div className='vertical-align'>
                        <a href='#/cart'>
                            <FontAwesomeIcon
                                id='mobile-nav-icon'
                                icon={faShoppingCart}
                            />
                        </a>
                    </div>
                </span>
            </nav>

            <input
                type='checkbox'
                id='toggleMenu'
                onChange={() => mobileNavAnimations(timelineRef)}
            />

            <div className='modal-overlay close'>
                <div className='ink-wrapper'>
                    <div className='ink-content'></div>
                </div>
            </div>

            <div className='modal'>
                <div className='mobile-nav'>
                    <label htmlFor='toggleMenu'>
                        <div className='close-menu' id='close-menu'>
                            <svg
                                xmlns='http://www.w3.org/2000/svg'
                                viewBox='0 0 128 128'
                                style={{
                                    height: "30px",
                                    width: "30px",
                                    margin: " -4px 0px 0px -6px",
                                }}
                                fill='#8ab6eb'
                            >
                                <path d='M96 59.609c0-7.984-6.199-14.459-13.83-14.459-1.514 0-2.977.255-4.349.734C77.78 37.921 71.611 31.5 64 31.5s-13.79 6.421-13.83 14.384a13.104 13.104 0 0 0-4.349-.734c-7.632 0-13.82 6.474-13.82 14.459 0 6.485 4.085 11.971 9.705 13.81-1.707 2.403-2.713 5.39-2.713 8.611 0 7.995 6.189 14.469 13.82 14.469 4.593 0 8.668-2.35 11.188-5.964 2.51 3.615 6.585 5.964 11.178 5.964 7.632 0 13.83-6.475 13.83-14.469 0-3.221-1.016-6.209-2.723-8.611C91.905 71.58 96 66.094 96 59.609zM64 76.641c-5.985 0-10.833-5.071-10.833-11.333 0-6.262 4.847-11.333 10.833-11.333 5.975 0 10.833 5.071 10.833 11.333 0 6.262-4.858 11.333-10.833 11.333z' />
                            </svg>
                        </div>
                    </label>

                    <div className='divider' id='divider'></div>
                    <ul className='mobile-ul'>
                        <li className='mobile-li' onClick={() => closeNav()}>
                            <a href='#/products'>Products</a>
                        </li>
                        <li
                            className='mobile-li'
                            onClick={() => {
                                if (checkAuth()) {
                                    closeNav();
                                }
                            }}
                        >
                            <GoogleAuthBtn
                                childComp={<a href='#/my-orders'>Orders</a>}
                            />
                        </li>
                        <li className='mobile-li' onClick={() => closeNav()}>
                            <a href='#/cart'>Cart</a>
                        </li>
                        <li className='mobile-li' onClick={() => closeNav()}>
                            <a href='#/faq'>FAQ</a>
                        </li>

                        {checkAuth() ? (
                            <li className='mobile-li' onClick={() => logout()}>
                                <FontAwesomeIcon
                                    style={{ color: "#fff" }}
                                    icon={faSignOutAlt}
                                />
                            </li>
                        ) : null}
                    </ul>
                </div>
            </div>
        </>
    );
};

const logout = () => {
    localStorage.setItem("iqj1", "");
    setAccessToken("");
    window.location.reload();
};

const closeNav = () => {
    try {
        const elem = document.getElementById("toggleMenu")! as any,
            doc = document,
            liArr = doc.getElementsByClassName("mobile-li") as any,
            divider = doc.getElementById("divider")!,
            flowerIcon = doc.getElementById("close-menu")!;

        elem.checked = false;

        for (let i = 0; i < liArr.length; i++) {
            liArr[i].style.opacity = 0;
            liArr[i].style.transform = "scale(0)";
        }

        divider.style.width = "0%";
        divider.style.opacity = "0";
        flowerIcon.style.opacity = "0";
    } catch {}
};

const mobileNavAnimations = timelineRef => {
    const elem = document.getElementById("toggleMenu")! as any;

    if (elem.checked) {
        setTimeout(() => {
            timelineRef.current = anime
                .timeline({})
                .add({
                    duration: 300,
                    targets: [".close-menu", ".divider"],
                    opacity: {
                        value: [0, 1],
                        easing: "linear",
                    },
                })

                .add(
                    {
                        duration: 650,
                        easing: "linear",
                        targets: ".divider",
                        width: ["0", "100%"],
                    },
                    -50
                )
                .add(
                    {
                        duration: function (_t, i) {
                            return 900 + i * 75;
                        },
                        targets: ".mobile-ul .mobile-li",
                        easing: "easeOutExpo",
                        opacity: {
                            value: [0, 1],
                            easing: "linear",
                        },
                        scale: [0, 1],
                        delay: anime.stagger(150, {
                            start: 100,
                        }),
                    },
                    -50
                );
        }, 800);
    } else {
        const doc = document,
            liArr = doc.getElementsByClassName("mobile-li") as any,
            divider = doc.getElementById("divider")!,
            flowerIcon = doc.getElementById("close-menu")!;

        for (let i = 0; i < liArr.length; i++) {
            liArr[i].style.opacity = 0;
            liArr[i].style.transform = "scale(0)";
        }

        divider.style.width = "0%";
        divider.style.opacity = "0";
        flowerIcon.style.opacity = "0";
    }
};

export default MobileNavbar;
