import { HashRouter as Router } from "react-router-dom";
import Navbar from "components/mobileNavbar/Navbar";
import "./css/app.scss";
import { Routes } from "routes/Routes";
import { useEffect, useState } from "react";
import URI from "URI";
import { setAccessToken } from "Auth/accessToken";

function App() {
    const [pageLoader, setPageLoader] = useState(true);
    const currentRefreshToken = localStorage.getItem("iqj1") as string;

    useEffect(() => {
        try {
            //get new accessToken & refreshToken with fetch to URI/refresh_token
            fetch(`${URI}/refresh_token`, {
                method: "POST",
                credentials: "include",
                headers: {
                    refreshToken: currentRefreshToken,
                },
            }).then(async res => {
                const { accessToken, refreshToken } = await res.json();
                setAccessToken(accessToken);
                localStorage.setItem("iqj1", refreshToken);
                setPageLoader(false);
            });
        } catch {
            setPageLoader(false);
        }
    }, []);

    return (
        <Router>
            <div className='App'>
                <Navbar />
                <div id='content'>
                    <Routes />
                </div>
            </div>
        </Router>
    );
}

export default App;
