import { setAccessToken } from "Auth/accessToken";
import { checkAuth } from "Auth/CheckAuth";
import { useLoginMutation } from "generated/graphql";
import { useGoogleLogin } from "react-google-login";

const GoogleAuthBtn: React.FC<{ childComp: JSX.Element | React.FC }> = ({
    childComp,
}) => {
    const [login] = useLoginMutation();

    const { signIn } = useGoogleLogin({
        clientId:
            "359730666759-3r522ktdv6em93g84i9tf6e6349ag55c.apps.googleusercontent.com",

        onSuccess: async (res: any) => {
            const result = await login({
                variables: {
                    user_id: res.googleId,
                    email: res.profileObj.email,
                },
            });

            setAccessToken(result.data?.login.accessToken!);
            localStorage.setItem("iqj1", result.data?.login.refreshToken!);
            window.location.reload();
        },
        onFailure: () => {},
    });

    return (
        <>
            {checkAuth() ? (
                <>{childComp}</>
            ) : (
                <span
                    onClick={() => {
                        signIn();
                    }}
                >
                    {childComp}
                </span>
            )}
        </>
    );
};

export default GoogleAuthBtn;
